.gallery {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;


  p {
    font-family: "Raleway", "Arial", sans-serif;
    font-weight: 800;
    font-size: 21px;
    line-height: 25px;
    color: #ffffff;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
    margin: 0;
    position: absolute;
    bottom: 37px;
    left: 40px;
    right: initial;
    max-width: 320px;

    @media (max-width: "1098px") {
      font-size: 18px;
      line-height: 21px;
      text-align: center;
      left: 0;
      right: 0;
      bottom: 55px;
      margin: auto;
      max-width: 250px;
    }

    @media (max-width: $mobile-width) {
      bottom: 45px;
    }
  }
}

.gallery__main-photo {
  position: relative;
  flex-shrink: 0;
  margin-right: 17px;
  transform: translate(3px, -67px);

  @media (max-width: 1098px) {
    margin: 0 auto;
    transform: none;
  }

  @media (max-width: $mobile-width) {
    font-size: 18px;
    margin: 0 auto;
  }

  img {
    @media (max-width: $mobile-width) {
      margin-bottom: 11px;
    }
  }
}

.gallery__photos {
  @include list-reset();
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  @media (max-width: 1098px) {
    display: none;
  }


  @media (max-width: $mobile-width) {
    display: none;
  }
}

.gallery__item {
  position: relative;
  margin-bottom: 25px;

  @media (max-width: 1338px) {
    width: 211px;
    height: 318px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--small {
    p {
      left: 24px;
      max-width: 120px;
    }
  }

  &--large {
    p {
      left: 32px;
      max-width: 320px;
    }
  }

  &--art {
    p {
      left: 32px;
      max-width: 360px;
    }
  }
}
